import { Component, OnDestroy } from '@angular/core';
import { Router } from '@angular/router';
import { AuthService } from '../../services/auth/auth.service';
import { DialogModalService } from '../../services/dialog-modal/dialog-modal.service';
import { Subscription } from 'rxjs/Subscription';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnDestroy {
  private session: any;
  private subscription: Subscription;
  
  constructor(
    private dialogModalService: DialogModalService,
    private authService: AuthService,
    private router: Router
  ) {
    this.subscription = this.authService.isAuthenticated().subscribe(session => {
      this.session = session.success;
    });
  }

  ngOnDestroy() {
    // unsubscribe to ensure no memory leaks
    this.subscription.unsubscribe();
  }

  private register() {
    this.dialogModalService.showRegisterModal()
  }

  private login() {
    this.dialogModalService.showLoginModal();
  }

  private logout() {
    this.authService.logout();
  }
}



