import { Component } from '@angular/core';
import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';
import { ValidateService } from '../../services/validate/validate.service';
import { AuthService } from '../../services/auth/auth.service';

export interface RegisterDialogModel {
  title: string;
  message: string;
}

export interface PayloadModel {
  success: boolean;
  data: any;
  validateMsg: string;
  authMsg: string;
}

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent
  extends DialogComponent<RegisterDialogModel, PayloadModel>
  implements RegisterDialogModel {

  title: string;
  message: string;

  // User registration form fields
  firstName: string;
  lastName: string;
  email: string;
  password: string;
  phoneNumber: string;

  // User registration form
  private registerForm = {
    firstName: undefined,
    lastName: undefined,
    email: undefined,
    password: undefined,
    phoneNumber: undefined
  }

  // Invalid fields messages
  private invalidRegisterFormMessages = {
    firstName: "Prnom doit contenir au moins 3 caractres",
    lastName: "Nom doit contenir au moins 3 caractres",
    email: "Adresse email non valide",
    phoneNumber: "Numro de tlphone non valide",
    password: "Mot de passe doit contenir au moins 8 caractres"
  }

  private formValidationMsg: String = null;

  constructor(
    dialogService: DialogService,
    private validateService: ValidateService,
    private authService: AuthService
  ) {
    super(dialogService);
  }

  private invalidMessagesMapping(invalidFields) {
    let data = new Object();
    invalidFields.forEach(field => {
      data[field] = this.invalidRegisterFormMessages[field];
    });
    return data;
  }

  private checkResult() {
    if (this.result !== undefined) {
      if (this.result.success) {
        this.close();
      } else {
        this.formValidationMsg = 'Champs non valides.'
      }
    }
  }

  confirm() {
    this.registerForm = {
      firstName: this.firstName,
      lastName: this.lastName,
      email: this.email,
      password: this.password,
      phoneNumber: this.phoneNumber
    }
    let validationResult = this.validateService.validateRegister(this.registerForm);
    if (validationResult.success) {
      // try to register user
      this.authService.registerUser(validationResult.data).subscribe(userRegistration => {
        this.result = {
          success: userRegistration.success,
          data: validationResult.data,
          validateMsg: null,
          authMsg: userRegistration.msg
        }
        this.checkResult();
      }, error => {
        console.error(error);
      });
    } else {
      // form fields not valid
      this.result = {
        success: validationResult.success,
        data: this.invalidMessagesMapping(validationResult.data),
        validateMsg: validationResult.msg,
        authMsg: null
      }
      this.checkResult();
    }
  }

  cancel() {
    this.result = {
      success: null,
      data: null,
      validateMsg: null,
      authMsg: null
    }
    this.close();
  }
}



