import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from './services/auth/auth.guard';

// Components
import { HomeComponent } from './components/home/home.component';
import { ProfileComponent } from './components/profile/profile.component';
import { DonorsListComponent } from './components/donors-list/donors-list.component';
import { RecipientsListComponent } from './components/recipients-list/recipients-list.component';
import { CriticalListComponent } from './components/critical-list/critical-list.component';

const routes: Routes = [
  { path: '', component: HomeComponent },
  { path: 'cas-critiques', component: CriticalListComponent },
  { path: 'receveurs', component: RecipientsListComponent },
  { path: 'donneurs', component: DonorsListComponent, canActivate:[AuthGuard] },
  { path: 'profil', component: ProfileComponent, canActivate:[AuthGuard] }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }



