import { Component } from '@angular/core';
import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';
import { ValidateService } from '../../services/validate/validate.service';
import { AuthService } from '../../services/auth/auth.service';

export interface ChangeProfileDetailsDialogModel {
  title: string;
  message: string;
  profileDetails: any;
}

export interface PayloadModel {
  success: boolean;
  data: any;
  validateMsg: string;
  authMsg: string;
}

@Component({
  selector: 'app-change-profile-details',
  templateUrl: './change-profile-details.component.html',
  styleUrls: ['./change-profile-details.component.css']
})
export class ChangeProfileDetailsComponent
  extends DialogComponent<ChangeProfileDetailsDialogModel, PayloadModel>
  implements ChangeProfileDetailsDialogModel {

  title: string;
  message: string;
  profileDetails: {
    firstName: string;
    lastName: string;
    gender: string;
    governorate: string;
    dateOfBirth: string;
    bloodGroup: string;
    email: string;
    phoneNumber: string;
    userCondition: string;
  };

  // Profile details form field types
  public _gender = ['Homme', 'Femme'];
  public _bloodGroup = [
    'A+', 'A-', 'B+', 'B-',
    'AB+', 'AB-', 'O+', 'O-'
  ];
  public _governorate = [
    'Ariana', 'Bja', 'Ben Arous', 'Bizerte', 'Gabs', 'Gafsa',
    'Jendouba', 'Kairouan', 'Kasserine', 'Kebili', 'Kef', 'Mahdia',
    'Manouba', 'Medenine', 'Monastir', 'Nabeul', 'Sfax', 'Sidi Bouzid',
    'Siliana', 'Sousse', 'Tataouine', 'Tozeur', 'Tunis', 'Zaghouan'
  ];
  public _userCondition = ['Donneur', 'En besoin', 'En urgence'];

  // Profile details form
  private detailsChangeForm = {
    firstName: undefined,
    lastName: undefined,
    gender: undefined,
    governorate: undefined,
    dateOfBirth: undefined,
    bloodGroup: undefined,
    phoneNumber: undefined,
    userCondition: undefined
  }

  // Invalid fields messages
  private invalidDetailsChangeFormMessages = {
    firstName: "Prnom doit contenir au moins 3 caractres",
    lastName: "Nom doit contenir au moins 3 caractres",
    dateOfBirth: "Date de naissance non valide",
    phoneNumber: "Numro de tlphone non valide"
  }

  private formValidationMsg: String = null;

  constructor(
    dialogService: DialogService,
    private validateService: ValidateService,
    private authService: AuthService
  ) {
    super(dialogService);
  }

  private invalidMessagesMapping(invalidFields) {
    let data = new Object();
    invalidFields.forEach(field => {
      data[field] = this.invalidDetailsChangeFormMessages[field];
    });
    return data;
  }

  private checkResult() {
    if (this.result !== undefined) {
      if (this.result.success) {
        this.close();
      } else {
        // this.formValidationMsg = 'Champs non valides.'
      }
    }
  }

  confirm() {
    let validationResult = this.validateService.validateUpdate(this.profileDetails);
    if (validationResult.success) {
      this.authService.updateUserProfile(this.profileDetails).subscribe(update => {
        this.result = {
          success: update.success,
          data: validationResult.data,
          validateMsg: null,
          authMsg: update.msg
        }
        this.checkResult();
      });
    } else {
      // form fields not valid
      this.result = {
        success: validationResult.success,
        data: this.invalidMessagesMapping(validationResult.data),
        validateMsg: validationResult.msg,
        authMsg: null
      }
      this.checkResult();
    }
  }

  cancel() {
    this.result = {
      success: null,
      data: null,
      validateMsg: null,
      authMsg: null
    }
    this.close();
  }
}



