import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../services/auth/auth.service';

@Component({
  selector: 'app-donors-list',
  templateUrl: './donors-list.component.html',
  styleUrls: ['./donors-list.component.css']
})
export class DonorsListComponent implements OnInit {
  private donors: Array<any>;

  constructor(
    private authService: AuthService
  ) { }

  ngOnInit() {
    this.authService.getDonorsList().subscribe(data => { 
      if (data.success) {
        this.donors = data.docs;
      }
    });
  }
}



