import { Component, OnInit } from '@angular/core';
import { AuthService } from '../../services/auth/auth.service';

@Component({
  selector: 'app-critical-list',
  templateUrl: './critical-list.component.html',
  styleUrls: ['./critical-list.component.css']
})
export class CriticalListComponent implements OnInit {
  private criticalCases: Array<any>;

  constructor(
    private authService: AuthService
  ) { }

  ngOnInit() {
    this.authService.getCriticalList().subscribe(data => { 
      if (data.success) {
        this.criticalCases = data.docs;
      }
    });
  }
}



