import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';

// Modules
import { AppRoutingModule } from './app-routing.module';
import { BootstrapModalModule } from 'ng2-bootstrap-modal';
import { SwiperModule } from 'angular2-useful-swiper';

// Services
import { DialogModalService } from './services/dialog-modal/dialog-modal.service';
import { ValidateService } from './services/validate/validate.service';
import { AuthService } from './services/auth/auth.service';
import { AuthGuard } from './services/auth/auth.guard';

// Components
import { AppComponent } from './app.component';
import { NavbarComponent } from './components/navbar/navbar.component';
import { HomeComponent } from './components/home/home.component';
import { RegisterComponent } from './components/register/register.component';
import { LoginComponent } from './components/login/login.component';
import { ProfileComponent } from './components/profile/profile.component';
import { DonorsListComponent } from './components/donors-list/donors-list.component';
import { RecipientsListComponent } from './components/recipients-list/recipients-list.component';
import { CriticalListComponent } from './components/critical-list/critical-list.component';
import { ChangeProfileDetailsComponent } from './components/change-profile-details/change-profile-details.component';

@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    HomeComponent,
    RegisterComponent,
    LoginComponent,
    ProfileComponent,
    DonorsListComponent,
    RecipientsListComponent,
    CriticalListComponent,
    ChangeProfileDetailsComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    AppRoutingModule,
    BootstrapModalModule.forRoot({container:document.body}),
    SwiperModule
  ],
  entryComponents: [
    RegisterComponent,
    LoginComponent,
    ChangeProfileDetailsComponent
  ],
  providers: [
    DialogModalService,
    ValidateService,
    AuthService,
    AuthGuard
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



