import { Injectable } from '@angular/core';
import { Http, Headers } from '@angular/http';
import { Observable } from 'rxjs';
import { Subject } from 'rxjs/Subject';

import 'rxjs/add/operator/map';

@Injectable()
export class AuthService {
  private authToken: any;
  private user = new Subject<any>();
  private session = new Subject<any>();

  constructor(private http: Http) {
    this.userSession().subscribe(session => {
      this.session.next({ success: session.success });
    });
  }

  registerUser(user) {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.post('http://localhost:3000/users/register', user, {headers: headers})
      .map(res => res.json());
  }

  authenticateUser(user) {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.post('http://localhost:3000/users/authenticate', user, {headers: headers})
      .map(res => {
        this.session.next(res.json());
        return res.json();
      });
  }

  userSession() {
    let headers = new Headers();
    this.localToken();
    headers.append('Authorization', this.authToken);
    headers.append('Content-Type', 'application/json');
    return this.http.get('http://localhost:3000/users/session', {headers: headers})
      .map(res => {
        this.session.next(res.json());
        return res.json();
      });
  }

  isAuthenticated() {
    return this.session.asObservable();
  }

  isUpdated() {
    return this.user.asObservable();
  }

  getUserProfile() {
    let headers = new Headers();
    this.localToken();
    headers.append('Authorization', this.authToken);
    headers.append('Content-Type', 'application/json');
    return this.http.get('http://localhost:3000/users/profile', {headers: headers})
      .map(res => {
        let _user = res.json().user;
        if (_user) {
          this.user.next(undefined);
          this.user.next( res.json().user);
        }
        return res.json();
      });
  }

  updateUserProfile(userUpdate) {
    let headers = new Headers();
    this.localToken();
    headers.append('Authorization', this.authToken);
    headers.append('Content-Type', 'application/json');
    return this.http.post('http://localhost:3000/users/update', userUpdate, {headers: headers})
      .map(res => {
        let _user = res.json().user;
        if (_user) {
          this.user.next(undefined);
          this.user.next( res.json().user);
        }
        return res.json();
      });
  }

  updateUserProfilePicture(userEmail, file, fileName) {
    let headers = new Headers();
    this.localToken();
    headers.append('Authorization', this.authToken);
    let formData: any = new FormData();
    formData.append("email", userEmail);
    formData.append("fileName", fileName);
    formData.append("uploads[]", file, fileName);
    return this.http.post('http://localhost:3000/users/upload', formData, {headers: headers})
      .map(res => {
        let _user = res.json().user;
        if (_user) this.user.next( res.json().user);
        return res.json();
      });
  }

  getDonorsList() {
    let headers = new Headers();
    this.localToken();
    headers.append('Authorization', this.authToken);
    headers.append('Content-Type', 'application/json');
    return this.http.get('http://localhost:3000/users/donors', {headers: headers})
      .map(res => res.json());
  }

  getCriticalList() {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.get('http://localhost:3000/users/critical', {headers: headers})
      .map(res => res.json());
  }

  getRecipientsList() {
    let headers = new Headers();
    headers.append('Content-Type', 'application/json');
    return this.http.get('http://localhost:3000/users/recipients', {headers: headers})
      .map(res => res.json());
  }

  localToken() {
    const token = localStorage.getItem('id_token');
    this.authToken = token;
  }

  storeUserData(token, user) {
    localStorage.setItem('id_token', token);
    localStorage.setItem('user', JSON.stringify(user));
    this.authToken = token;
    this.user = user;
  }

  logout() {
    this.authToken = null;
    this.user = null;
    localStorage.clear();
    this.session.next({success: false});
  }
}



