import { Component } from '@angular/core';
import { DialogComponent, DialogService } from 'ng2-bootstrap-modal';
import { ValidateService } from '../../services/validate/validate.service';
import { AuthService } from '../../services/auth/auth.service';

export interface LoginDialogModel {
  title: string;
  message: string;
}

export interface PayloadModel {
  success: boolean;
  data: any;
  validateMsg: string;
  authMsg: string;
}

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent
  extends DialogComponent<LoginDialogModel, PayloadModel>
  implements LoginDialogModel {

  title: string;
  message: string;

  // User login form fields
  email: string;
  password: string;

  // User login form
  private loginForm = {
    email: undefined,
    password: undefined
  }

  // Invalid fields messages
  private invalidLoginFormMessages = {
    email: "Adresse email non valide",
    password: "Mot de passe doit contenir au moins 8 caractres"
  }

  // private formValidationMsg: String = null;

  constructor(
    dialogService: DialogService,
    private validateService: ValidateService,
    private authService: AuthService
  ) {
    super(dialogService);
  }

  private invalidMessagesMapping(invalidFields) {
    let data = new Object();
    invalidFields.forEach(field => {
      data[field] = this.invalidLoginFormMessages[field];
    });
    return data;
  }

  private checkResult() {
    if (this.result !== undefined) {
      if (this.result.success) {
        this.close();
      } else {
        // this.formValidationMsg = 'Champs non valides.'
      }
    }
  }

  confirm() {
    this.loginForm = {
      email: this.email,
      password: this.password
    }
    let validationResult = this.validateService.validateLogin(this.loginForm);
    if (validationResult.success) {
      // try to login user
      this.authService.authenticateUser(validationResult.data).subscribe(userSession => {
        if (userSession.success)
          this.authService.storeUserData(userSession.token, userSession.user);
        this.result = {
          success: userSession.success,
          data: validationResult.data,
          validateMsg: null,
          authMsg: userSession.msg
        }
        this.checkResult();
        window.location.reload(true);
      }, error => {
        console.error(error);
      });
    } else {
      // form fields not valid
      this.result = {
        success: validationResult.success,
        data: this.invalidMessagesMapping(validationResult.data),
        validateMsg: validationResult.msg,
        authMsg: null
      }
      this.checkResult();
    }
  }

  cancel() {
    this.result = {
      success: null,
      data: null,
      validateMsg: null,
      authMsg: null
    }
    this.close();
  }
}



