import { Component, OnInit, OnDestroy } from '@angular/core';
import { AuthService } from '../../services/auth/auth.service';
import { DialogModalService } from '../../services/dialog-modal/dialog-modal.service';
import { Subscription } from 'rxjs/Subscription';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit, OnDestroy {
  private session: any = true;
  private subscription: Subscription;
  swiperConfig: Object = {
    loop: true,
    effect: 'fade',
    fade: {
      crossFade: true
    },
    speed: 3000,
    autoplay: true,
    pagination: '.swiper-pagination',
    paginationClickable: true,
    nextButton: '.swiper-button-next',
    prevButton: '.swiper-button-prev',
    spaceBetween: 30
  };
  
  constructor(
    private dialogModalService: DialogModalService,
    private authService: AuthService
  ) {
    this.subscription = this.authService.isAuthenticated().subscribe(session => {
      this.session = session.success;
      console.log(this.session);
    });
  }

  ngOnInit() {
  }

  ngOnDestroy() {
    // unsubscribe to ensure no memory leaks
    this.subscription.unsubscribe();
  }

  private register() {
    this.dialogModalService.showRegisterModal()
  }

}



