import { Injectable } from '@angular/core';
import { DialogService } from 'ng2-bootstrap-modal';
import { AuthService } from '../auth/auth.service';
import { RegisterComponent } from '../../components/register/register.component';
import { LoginComponent } from '../../components/login/login.component';
import { ChangeProfileDetailsComponent } from '../../components/change-profile-details/change-profile-details.component';

@Injectable()
export class DialogModalService {
  private openedModal: boolean = false;

  constructor(
    private dialogService: DialogService,
    private authService: AuthService
  ) { }

  showRegisterModal() {
    if (!this.openedModal) {
      this.openedModal = true;
      let disposable = this.dialogService.addDialog(RegisterComponent, {
        title: 'Crer un compte',
        message: ''
      }, { backdropColor: 'rgba(0, 0, 0, 0.67)' }).subscribe(payload => {
        // We get dialog result
        if (payload.success !== null) {
          // isConfirmed
          this.openedModal = !payload.success;
          return true;
        } else {
          // canceled
          this.openedModal = false;
          return false;
        }
      });
    }
  }

  showLoginModal() {
    if (!this.openedModal) {
      this.openedModal = true;
      let disposable = this.dialogService.addDialog(LoginComponent, {
        title: 'Se connecter',
        message: ''
      }, { backdropColor: 'rgba(0, 0, 0, 0.67)' }).subscribe(payload => {
        // We get dialog result
        if (payload.success !== null) {
          // isConfirmed
          this.openedModal = !payload.success;
          return true;
        } else {
          // canceled
          this.openedModal = false;
          return false;
        }
      });
    }
  }

  showChangeProfileDetails(details) {
    if (!this.openedModal) {
      // check if user is authenticated before doing anything...
      this.authService.userSession().subscribe(userSession => {
        if (userSession.success) {
          this.openedModal = true;
          let disposable = this.dialogService.addDialog(ChangeProfileDetailsComponent, {
            title: 'Editer mon profil',
            message: '',
            profileDetails: details
          }, { backdropColor: 'rgba(0, 0, 0, 0.67)' }).subscribe(payload => {
            // We get dialog result
            if (payload.success !== null) {
              // isConfirmed
              this.openedModal = !payload.success;
            } else {
              // canceled
              this.openedModal = false;
            }
          });
        }
      });
    }
  }
}



