import { Component, OnInit, OnDestroy } from '@angular/core';
import { DialogModalService } from '../../services/dialog-modal/dialog-modal.service';
import { AuthService } from '../../services/auth/auth.service';
import { Subscription } from 'rxjs/Subscription';

@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit, OnDestroy {
  filesToUpload: Array<File> = [];
  private user: any;
  private subscription: Subscription;

  constructor(
    private dialogModalService: DialogModalService,
    private authService: AuthService
  ) {
    this.subscription = this.authService.isUpdated().subscribe(data => {
      this.user = data;
    });
  }

  private profileDetails = {
    email: undefined, // used as user identifier
    firstName: "Prnom",
    lastName: "Nom",
    gender: "Sexe",
    governorate: "Gouvernorat",
    dateOfBirth: "Date de naissance",
    bloodGroup: "Groupe sanguin",
    phoneNumber: "Numro de tlphone",
    userCondition: "Type du profil"
  };

  ngOnInit() {
    this.authService.getUserProfile().subscribe(data => { 
      if (data.success) {
        this.user = data.user;
        // map user properties to profile details
        for (var property in this.user) {
          if (this.profileDetails.hasOwnProperty(property))
            this.profileDetails[property] = this.user[property];
        }
      }
    });
  }

  ngOnDestroy() {
    // unsubscribe to ensure no memory leaks
    this.subscription.unsubscribe();
  }

  private changeProfileDetails() {
    this.dialogModalService.showChangeProfileDetails(this.profileDetails);
  }

  private changeProfilePicture(fileInput: any) {
    let files: Array<File> = <Array<File>>fileInput.target.files;
    this.authService.updateUserProfilePicture(
      this.user.email, files[0], files[0]['name']
    ).subscribe(data => {
        // uploaded successfully
      }, error => {
        console.error(error);
    });
  }
}



